import util from '@/common/util.js';
import utilCore from '@/api/utilCore.js';
//封装保存本地数据操作
module.exports = {
	//保存我的数据
	setMyInfo: function(data){uni.setStorage({
		key:'setMyInfo',
		data:data
	})},
	
	//获取我的数据
	getMyInfo: function(){
		utilCore.autoLoginUi();
		return uni.getStorageSync('setMyInfo');
	},
	
	 //退出登录
	 outLogin: function(){
	 	uni.removeStorage({
	 		key:'setMyInfo'
	 	})
	 }
} 
